@import url(https://fonts.googleapis.com/css?family=VT323);
body,html{width:100%;height:100%}body{background-color:#000;font-size:28px;font-family:sans-serif;color:#fff;display:flex;margin:0;padding:0}p{margin:0}#root{width:100%}.App{display:flex;width:100%;height:100%;font-family:"VT323";background:#000;font-size:28px}#terminal{margin:auto;width:1000px;height:100%;color:#18ff62;text-shadow:rgba(10,255,10,0.8);-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}#terminal p{-webkit-animation:blur 5s linear infinite, glow 1s linear infinite;animation:blur 5s linear infinite, glow 1s linear infinite}.header{line-height:1}.attempts{margin:15px 0 15px}.character:hover{background-color:#18ff62;color:black}@-webkit-keyframes blur{50%{color:#00e449;text-shadow:2px 0 2px rgba(163,255,163,0.8)}}@keyframes blur{50%{color:#00e449;text-shadow:2px 0 2px rgba(163,255,163,0.8)}}@-webkit-keyframes glow{50%{transform:scale(1.002)}}@keyframes glow{50%{transform:scale(1.002)}}

